.loginPage {
    width: 100%;
    text-align: center;
    padding-top: 40vh;
}

.btn {
  border-width: 0;
  outline: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: large;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .6);

  background-color: #1a2035;
  color: #ecf0f1;
  
  transition: background-color .3s;
}
.btn:hover, .btn:focus {
    cursor: pointer;
    background-color: #131726;
}